@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');



.main-section{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 90%;
    height: 80vh;
    position: relative;

    
}

.balls{
    position: absolute;
    top: 290px;
    left: -50px;
    animation: slide 2s  ease;

}


.balls1{
    position: absolute;
    top: 10px;
    right: -150px;
    z-index: -1;
    animation: slide 1s  ease;
}

.balls2{
    position: absolute;
    top: 10px;
    right: 700px;
    z-index: -1;
    animation: slide 2s ease;

}


.balls3{
    position: absolute;
    top: 300px;
    right: 400px;
    z-index: -1;
    animation: slide 1s  ease;

}

.text{
    width: 45%;
    display: flex;
    flex-direction: column;
    justify-content: right;
    align-items: end;
    color: black;
    background: linear-gradient(270deg, #097217,#0b2009);
    background-clip: text;
    color: transparent;
    

}

.text h1{
    font-size: 5rem;
    margin-bottom: 0;
    text-align: right;
    /* color: black; */
    font-family: "Anton", sans-serif;
    /* text-shadow: 2px 2px 20px greenyellow; */
}

.text p{
    /* border: 1px solid black; */
    margin-left: 20px;
    text-align: justify;
}


.buttons{
    display: flex;
    width: fit-content;
    height: fit-content;
    gap: 1rem;
    /* border: 1px solid green; */
    /* border: 9px solid green; */

    /* width: 50%; */
}
.button-style1{
    background-color: greenyellow;
    cursor: pointer;
    border: none;
    color: green;
    width: 90px;
    padding: 6px;
    font-size: 0.8rem;
    border-radius: 7px;
   


   
    &:hover{
        cursor: pointer;
        font-weight: 600;
    }

}

.image{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 45%;
   
}

.image img{
    width: 800px;
    height: 600px;
    filter: drop-shadow(2px 2px 20px black);
    
}

@keyframes slide {
    0%{
        opacity: 0;
        transform: translateY(-100%);
    }
    100%{
        opacity: 1;
        transform: translateY(0);
    }
}


::-webkit-scrollbar{
    display: none;
}


