
.navbar{
    display: flex;
    flex-direction: row;
    justify-content: center;
    overflow-x: hidden;
    align-items: center;
    width: 100%;
    height: 5rem;
    /* background-color: red; */
    /* border: 2px solid red; */
}

.navbar-style{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    cursor: pointer;
}

.navbar-style img{
    width: 100px;
    height: 100%;
    object-fit: cover;
}

.navbar-style ul{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    list-style: none;
    width: 50%;
    text-align: center;
    cursor: pointer;

   

}

.navbar-style ul li{
    /* width: 50%; */
    padding: 5px;
    /* border: 2px solid green; */
    color: rgb(8, 36, 4);
    font-size: 1.2rem;
    font-weight: 500;

    &:hover{
        border-bottom: 2px solid greenyellow;
    }
}

.button-style{
    background-color: greenyellow;
    border: none;
    color: white;
    width: 70px;
    padding: 5px;
    color: green;

    &:hover{
        color: black;
        font-weight: 700;
        cursor: pointer;
    }
}

@media screen  and (max-width:600px){
    
.navbar{
    display: flex;
    flex-direction: row;
    justify-content: center;
    overflow-x: hidden;
    align-items: center;
    width: 100%;
    height: 5rem;
    /* background-color: red; */
    /* border: 2px solid red; */
}

.navbar-style{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 90%;
    cursor: pointer;
    /* border: 2px solid red; */
}

.navbar-style img{
    width: 80px;
    height: 50%;
    object-fit: cover;
    
}

.navbar-style ul{
    display: flex;
    flex-direction: row;
    justify-content:center;
    align-items: center;
    list-style: none;
    width: 100%;
    text-align: center;
    cursor: pointer;
    margin-right: 0px;
    gap: 5px;

   

}

.navbar-style ul li{
  
    
    padding: 2px;
    /* border: 2px solid green; */
    width: 100%;
    color: rgb(8, 36, 4);
    font-size: 0.8rem;
    font-weight: 500;

    &:hover{
        border-bottom: 2px solid greenyellow;
    }
}

.button-style{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: greenyellow;
    border: none;
    color: white;
    width: 70px;
    padding: 2px;
    color: green;
    margin-left: 5px;

    &:hover{
        color: black;
        font-weight: 700;
        cursor: pointer;
    }
}
}